import * as React from 'react';
import { TextArea } from 'oned-react';

class ControlledDemo extends React.Component<any, any> {
  state = { value: null };

  handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    this.setState({
      value: e.target.value,
    });
  };

  render() {
    return (
      <TextArea
        placeholder="通过 exceedHint props 来修改超出时的文案"
        onChange={this.handleChange}
        value={this.state.value}
        maxLength={10}
        exceedHint="兄第，你溢出了"
      />
    );
  }
}

export default <ControlledDemo />;

